

export const enum SizeType {
   Volume = "volume",
   Weight = "weight",
}

export const enum ProductCategory {
   All = "",
   BodyCare = "  ",
   HandCare = "  ",
   FeetCare = "  ",
   FaceCare = "  ",
   HairCare = "  ",
   TanningProducts = "  ",
   ShavingProducts = "  ",
   GiftSets = " ",
   HygieneProducts = " ",
   OralHygiene = "  ",
   PaperProducts = " "
}

export interface IProduct {
   img: string;
   name: string;
   sizeType: SizeType;
   size: number;
   barcode: number;
   manufacturer: string;
   brand: string;
   description: string;
   price: number;
   categories: ProductCategory[];
}


export interface ProductState {
   products: IProduct[]
}

export enum ProductActionTypes {
   GET_PRODUCTS = 'GET_PRODUCTS',
}


interface GetProductAction {
   type: ProductActionTypes.GET_PRODUCTS;
   payload: IProduct[];
}



export type ProductAction = GetProductAction

